<template>
  <div class="cwrapper">
    <div class="socialEcoTitle">
      <div class="sociltitlehead">社区动态</div>
      <div>
        <img src="../../assets/imgs/point1 (1).png" alt />
      </div>
    </div>
    <div class="communityTable" v-for="(v,i) in rows" :key="i">
      <img style="width:100%" :src="v.image" alt />
      <div class="title">{{v.title}}</div>
      <div class="hintMessage">
        <div class="type">
          <img src="../../assets/img_community/新闻iocn.png" alt />
          <div>{{v.dynamicType}}</div>
        </div>
        <div class="date">
          <img src="../../assets/img_community/时间iocn.png" alt />
          <div>{{v.createTime}}</div>
        </div>
        <div class="mannager">
          <img src="../../assets/img_community/管理iocn.png" alt />
          <div>管理员</div>
        </div>
      </div>
      <div v-html="v.content" class="content">{{v.content}}</div>
    </div>
    <button @click="getMore()" class="readMore">查看更多</button>
  </div>
</template>

<script>
import moment from "moment/moment";
import { content } from "../../../api";
export default {
  mounted() {
    let p = this.allpage;
    content(1, p, 2)
      .then(res => {
        for (let v of res.data.rows) {
          // var date = new Date(v.createTime);
          // var year = date.getFullYear();
          // var month =
          //   date.getMonth() + 1 < 10
          //     ? "0" + (date.getMonth() + 1)
          //     : date.getMonth() + 1;
          // var day = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();

          // v.createTime = year + "/" + month + "/" + day;
          v.createTime = moment(v.createTime).format("YYYY/MM/DD");
        }
        this.rows = res.data.rows;
      })
      .catch(err => {
        window.console.log(err);
      });
  },
  data() {
    return {
      rows: [],
      allpage: 1
    };
  },
  methods: {
    getMore() {
      this.allpage += 1;
      let p = this.allpage;
      content(1, p, 2)
        .then(res => {
          for (let v of res.data.rows) {
            // var date = new Date(v.createTime);
            // var year = date.getFullYear();
            // var month =
            //   date.getMonth() + 1 < 10
            //     ? "0" + (date.getMonth() + 1)
            //     : date.getMonth() + 1;
            // var day =
            //   date.getDate() < 10 ? "0" + date.getDate() : date.getDate();

            // v.createTime = year + "/" + month + "/" + day;
            v.createTime = moment(v.createTime).format("YYYY/MM/DD");
          }
          this.rows = res.data.rows;
        })
        .catch(err => {
          window.console.log(err);
        });
    }
  }
};
</script>

