  background: #f4f4f4;
  .title {
    font-size: 0.32rem;
    color: #4c4c4c;
    margin-top: 0.66rem;
    margin-bottom: 0.66rem;
  }
  .hintMessage {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.64rem;
    & > div {
      width: 2rem;
      height: 0.66rem;
      border: 0.02rem solid #9b9b9f;
      border-radius: 0.1rem;
      display: flex;
      align-items: center;
      justify-content: space-around;
      font-size: 0.24rem;
      color: #9b9b9f;
      padding: 0 0.2rem;
    }
    .type {
      img {
        width: 0.34rem;
        height: 0.29rem;
      }
    }
    .date {
      img {
        width: 0.34rem;
        height: 0.34rem;
      }
    }
    .mannager {
      img {
        width: 0.25rem;
        height: 0.28rem;
      }
    }
  }
  .content {
    font-size: 0.16rem;
    color: rgba(0, 0, 0, 0.7);
    margin-bottom: 0.59rem;
  }
}
.socialEcoTitle {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  padding-bottom: 0.67rem;
  // margin-top: 1.11rem;
  border-bottom: 0.01rem solid #737373;
  margin-bottom: 0.64rem;
  .sociltitlehead {
    font-size: 0.51rem;
    color: #4c4c4c;
  }
}
.readMore {
  width: 100%;
  height: 0.66rem;
  text-align: center;
  line-height: 0.66rem;
  font-size: 0.24rem;
  color: #fff;
  border: none;
  outline: none;
  cursor: pointer;
  background: #ff7000;
}
</style>